import tokenRequest from '@/utils/tokenRequest'

export function checkOutOrder (mode, obj) {
  // 立即结算与购物车一起结算
  // 传入自己必须的参数
  return tokenRequest.request({
    url: '/checkout/order',
    method: 'get',
    params: {
      mode: mode,
      delivery: 0,
      couponId: 0,
      isUsePoints: 0,
      payType: 10,
      ...obj
    }
  })
}

// 提交订单
export function checkoutSubmit (mode, obj) {
  return tokenRequest.request({
    url: '/checkout/submit',
    method: 'post',
    data: {
      mode: mode,
      delivery: 0,
      couponId: 0,
      isUsePoints: 0,
      payType: 10,
      ...obj
    }
  })
}

// 订单支付,服务器停止了对付款的处理,提交订单后直接是付款状态
export function orderPay (orderId, payType) {
  return tokenRequest.get('/order/pay', {
    params: {
      orderId,
      payType
    }
  })
}
