<template>
  <div class="box">
      <header>
          <NavBar>
          <template #go>
              <van-icon name="arrow-left" />
          </template>
          <template #title>
              订单结算台
          </template>
      </NavBar>
      </header>
      <main>
          <div class="adress" @click="toWhere">
              <div class="left">
                  <van-icon name="logistics" />
              </div>
              <div class="middle" v-if="defaultAdress.name">
                  <div class="title">
                      <span class="name ellipsisOneRow">{{ defaultAdress.name}}</span>
                      <span class="tel">{{ defaultAdress.phone}}</span>
                  </div>
                  <div class="userAdress ellipsisOneRow">
                     {{city}}
                  </div>
              </div>
              <div class="middle" v-else>请选择地址</div>
              <div class="right">
                  <van-icon name="arrow" />
              </div>
          </div>
          <div class="goodsList">
              <div class="goodsItem" v-for="item in goodsList" :key="item.goods_id">
                  <div class="left">
                      <img :src="item.goods_image" alt="">
                  </div>
                  <div class="right">
                      <div class="title">
                          <h3 class="ellipsis">{{item.goods_name}}</h3>
                      </div>
                      <div class="price">
                          <span>×{{item.total_num}}</span>
                          <span>￥{{item.goods_price_min}}</span>
                      </div>
                  </div>
              </div>
          </div>
          <div class="total">
              <p>共{{order.orderTotalNum}}件商品，合计：<span>￥{{order.orderPrice}}</span></p>
          </div>
          <div class="Prefernettial">
              <div><span>订单总金额：</span><span class="fontColor">￥{{order.orderTotalPrice}}</span></div>
              <div><span>优惠券：</span><span>无优惠券可用</span></div>
              <div><span>配送费用</span><span class="fontColor">+ ￥ 0.00</span></div>
          </div>
          <div class="pay">
            <div class="payment">支付方式</div>
            <div class="payselect">
                <div class="left">
                    <img src="@/assets/pay.png" alt="">
                    <span>余额支付</span>
                    <strong>(可用￥{{personal.balance}}元)</strong>
                </div>
                <div class="right">
                    <van-icon name="success" color="red"/>
                </div>
            </div>
          </div>
          <div class="rekmen">
            <input type="text" placeholder="选填: 买家留言（50字以内）">
          </div>
      </main>
      <footer>
          <div class="submit">
              <div class="left">
                  实付款：<span class="fontColor">￥ {{order.orderPayPrice}}</span>
              </div>
              <div class="right">
                  <button @click="getAndPost('提交')">提交订单</button>
              </div>
          </div>
      </footer>
  </div>
</template>

<script>
import { checkOutOrder, checkoutSubmit, orderPay } from '@/api/pay'
import { Toast } from 'vant'
export default {
  name: 'payIndex',
  data () {
    return {
      defaultAdress: {},
      goodsList: [],
      order: {},
      personal: {},
      setting: {}
    }
  },
  methods: {
    // 获取数据与提交数据都用侧方法
    async getAndPost (num) {
      const query = this.$route.query
      //   存储对应的数据，如cartid,立即购买的商品id之类的
      let obj = {}
      if (query.mode === 'cart') {
        obj = { cartIds: query.cartIds }
      } else if (query.mode === 'buyNow') {
        obj = {
          goodsId: query.goodsId,
          goodsNum: query.goodsNum,
          goodsSkuId: query.goodsSkuId
        }
      }
      if (num === '渲染') {
        this.checkOutOrderAction(query.mode, obj)
      } else {
        this.checkoutSubmitAction(query.mode, obj)
      }
    },
    // 渲染
    async checkOutOrderAction (mode, obj) {
      const { data } = await checkOutOrder(mode, obj)
      console.log(data)
      // 如果没有默认地址,后端返回的是null,注意null不能取值,否则错误,而空对象则访问不存在的属性则会报错
      this.defaultAdress = data.order.address || {}
      this.goodsList = data.order.goodsList
      this.personal = data.personal
      this.order = data.order
      this.setting = data.setting
    },
    // 提交
    async checkoutSubmitAction (mode, obj) {
      if (this.defaultAdress.name && this.goodsList.length !== 0) {
        const { data: { orderId, payType } } = await checkoutSubmit(mode, obj)
        console.log(orderId, payType)
        // // 如果成功了这里目前直接购买,服务器停止了对付款的处理,提交订单后直接是付款成功状态
        // this.orderPayAction(orderId, payType)
        Toast.success('支付成功')
        this.$router.replace('/myorder')
      } else if (this.goodsList.length !== 0) {
        Toast('没有商品')
      } else {
        Toast('请选择地址')
      }
    },
    // 付款,服务器停止了对付款的处理,提交订单后直接是付款状态
    async orderPayAction (orderId, payType) {
      const res = await orderPay(orderId, payType)
      console.log(res)
    },
    toWhere () {
      this.$router.push({
        path: '/adresslist',
        query: {
          mode: this.mode,
          cartIds: this.idGroup
        }
      })
    }
  },
  computed: {
    city () {
      const region = this.defaultAdress.region || {}
      return Object.values(region).join(' ') + this.defaultAdress.detail
    }
  },
  created () {
    this.getAndPost('渲染')
  }
}
</script>
<!-- 判断有没有地址，有地址则条没地址则提示 -->
<style lang="less" scoped>
.fontColor {
  color: red;
}
footer {
  position: fixed;
  background: #fff;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;

  .submit {
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      height: 50px;
      display: flex;
      border: 1px solid #ececec;
      font-size: 14px;
      .left {
          padding-left: 15px;
          flex: 1;
          display: flex;
          justify-content: flex-start;
          align-items: center;

      }
      .right {
          width: 30%;
          button {
              width: 100%;
              height: 100%;
              border: 0;
              background: linear-gradient(to right, #ff361e, #ff6b2e);
              color: #fff;
          }
      }
  }
}
main {
  overflow: auto;
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  bottom: 50px;
  .adress {
    background-color: #fff;
      margin-top: 20px;
      border-bottom: 1px solid #ececec;
      padding: 0 20px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 70px;
      .left,
      .right {
          font-size: 14px;
      }
      .middle {
          display: flex;
          flex-direction: column;
          margin-left: -25px;
          font-size: 14px;
          width: 70%;
      }

  }
  .goodsList {
    background-color: #fff;
  }
  .goodsItem {
      display: flex;
      padding: 15px;
      .left {
          margin-right:20px;
          margin-left: 7px;
          img {
              width: 80px;
          }
      }
      .right {
         .title {
          height: 50px;
          h3 {
              padding-right: 5px;
              font-size: 14px;
              font-weight: 500;
          }
         }
         .price {
          display: flex;
          justify-content: space-between;
          font-size: 16px;
          span {
              &:nth-child(2) {
                  color: red;
              }
          }
         }
      }
  }
  .total {
    background-color: #fff;
      border-top: 1px solid #ececec;
    margin-bottom: 10px;
      p {
          font-size: 14px;
          padding:0 15px;
          height: 40px;
          line-height: 40px;
          text-align: right;
          span {
              color:red;
          }
      }
  }
  .Prefernettial {
    background-color: #fff;
      padding:0 15px;
      >div {
          height: 40px;
          line-height: 40px;
          font-size: 14px;
          display: flex;
          justify-content: space-between;
          align-items: center;
      }
      div {
        &:nth-child(1) {
            border-bottom: 1px solid #ececec;
        }
      }
  }
  .pay {
    padding:0 15px;
    margin-top: 10px;
    background-color: #fff;
    height: 80px;
    display: flex;
    flex-direction: column;
    font-size: 14px;
    .payment {
        height: 40px;
        line-height: 40px;
    }
    .payselect {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: space-between;
        img {
            width: 18px;
            vertical-align: middle;
        }
        span {
            margin: 0 10px;
        }
        strong {
            font-weight: 400;
        }
    }
  }
  .rekmen {
    background-color: #fff;
    padding:0 15px;
    height: 40px;
    display: flex;
    align-items: center;
    font-size: 14px;
    input {
        width: 100%;
        outline: none;
        border: 0;
    }

  }
}
</style>
